body {
    font-family: "Proxima Nova Rg";
    font-size: rem(16);
    color: #000;
    background-color: #fff;
}


h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1180));
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
    transition: all 0.5s ease-in-out;

    &:hover {
        opacity: 0.8;
    }
}

.section {
    &__title {
        box-sizing: border-box;
        position: relative;
        width: fit-content;
        margin: 0 auto rem(50);
        padding: rem(15) rem(15);
        font-family: "Neometric Alt", sans-serif;
        font-size: rem(45);
        font-weight: 600;
        line-height: 1.1;
        text-align: center;

        border-style: solid;
        border-width: 6px;
        border-image: url("../img/svg/border.svg")  5 0 5 0 repeat repeat;

        @media (max-width: 991px) {
            margin: 0 auto rem(35);
            padding: rem(12) rem(25);
            font-size: rem(30);
        }

        @media (max-width: 767px) {
            margin: 0 auto rem(30);
            padding: rem(10) rem(25);
            font-size: rem(25);
        }

        &:after {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

            border-style: solid;
            border-width: 6px;
            border-image: url("../img/svg/border-ver.svg")   0 5 0 5 repeat round;
        }

        .b_top {
            z-index: 10;
            top: rem(-12);
            left: rem(-6);

            @media (max-width: 575px) {
                display: none;
            }
        }
        .b_top-right {
            z-index: 10;
            top: rem(-12);
            right: rem(-18);

            @media (max-width: 575px) {
                display: none;
            }
        }
        .b_bottom {
            z-index: 10;
            bottom: rem(-12);
            left: rem(-6);

            @media (max-width: 575px) {
                display: none;
            }
        }
        .b_bottom-right {
            z-index: 10;
            right: rem(-18);
            bottom: rem(-12);

            @media (max-width: 575px) {
                display: none;
            }
        }

        &-border {
            content: "";
            position: absolute;
            width: rem(18);
            height: rem(18);
            background-image: url("data:image/svg+xml,%3Csvg width='1148' height='110' viewBox='0 0 1148 110' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='1134' width='18' height='18' fill='%2327A3DD'/%3E%3Crect x='1134' y='96' width='14' height='14' fill='%2327A3DD'/%3E%3Crect y='95' width='14' height='14' fill='%2327A3DD'/%3E%3Crect y='2' width='18' height='18' fill='%2327A3DD'/%3E%3C/svg%3E");
            background-repeat: no-repeat;

            @media (max-width: 991px) {
                width: rem(10);
                height: rem(10);
            }

            @media (max-width: 575px) {
                width: rem(5);
                height: rem(5);
            }
        }
    }
}

.img-fluid {
    max-width: 100%;
    height: auto;
}