.header {
    &__top {
        padding-top: rem(8);
        padding-bottom: rem(8);
        font-size: rem(12);
        line-height: 1.4;
        color: var(--black-color);
        background: #eff4ff;

        @media (max-width: 991px) {
            display: none;
        }

        &-wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &-info {
            display: flex;
            align-items: center;

            &__item {
                display: flex;
                align-items: center;

                &:not(:last-child) {
                    margin-right: 45px;
                }

                a {
                    color: var(--black-color);
                }

                span {
                    display: inline-block;
                    margin-right: rem(8);
                    background-size: contain;
                    background-repeat: no-repeat;

                    &.ic-clock {
                        width: rem(16);
                        height: rem(16);
                        background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='8' cy='8' r='7.5' stroke='%232E8FBC'/%3E%3Cpath d='M8 4V9.6L11.2 10.8' stroke='%232E8FBC'/%3E%3C/svg%3E");
                    }

                    &.ic-mail {
                        width: rem(16);
                        height: rem(11);
                        background-image: url("data:image/svg+xml,%3Csvg width='18' height='13' viewBox='0 0 18 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1V12H17V1M1 1H17M1 1L8.74194 7.36842L17 1' stroke='%232E8FBC'/%3E%3C/svg%3E");
                    }
                }
            }
        }

        &-phone {

            display: flex;
            align-items: center;

            a {
                font-family: "Neometric Alt", sans-serif;
                font-size: rem(14);
                font-weight: 600;
                color: var(--black-color);
            }

            &-text {
                margin-left: rem(15);
            }
        }
    }

    &__bottom {
        padding: rem(25) 0;

        &-wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    &__logo {

        img {
            @media (max-width: 767px) {
                height: rem(50);
            }

            @media (max-width: 574px) {
                height: rem(36);
            }
        }
    }

    &__menu {
        @media (max-width: 991px) {
            display: none;

            &._active {
                display: block;
                position: fixed;
                z-index: 1000;
                top: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                max-width: rem(380);
                overflow-y: auto;
                background-color: #c3e6f5;

                .header__menu-wrapper {
                    padding: rem(87) rem(15) rem(40) rem(48);
                }

                .header__menu-close {
                    content: " ";
                    display: block;
                    position: absolute;
                    top: rem(44);
                    right: rem(40);
                    cursor: pointer;
                }
            }

            &-overlay {
                display: none;
                position: fixed;
                z-index: 1;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: #000000;
                opacity: 0.5;

                &._active {
                    display: block;
                }
            }

            &-wrapper {
                z-index: 10;
            }
        }

        &-close {
            display: none;
        }

        &-sub-list {
            display: none;
        }

        &-sub-item {
            margin-bottom: rem(12);
            &:first-child {
                margin-top: rem(20);
            }
        }

        &-sub-link {
            font-size: rem(14);
            font-weight: normal;
            line-height: 140%;
            color: #000000;
        }

        &-list {
            @media (min-width: 992px) {
                display: flex;
                align-items: center;

                .header__menu-item {
                    position: relative;

                    &:not(:last-child) {
                        margin-right: rem(43);

                        @media (max-width: 1199px) {
                            margin-right: rem(25);
                        }
                    }

                    &:hover {
                        .header__menu-sub-list {
                            display: block;
                        }
                    }

                    .header__menu {
                        &-sub {
                            &-list {
                                box-sizing: border-box;
                                display: none;
                                position: absolute;
                                z-index: 1;
                                top: 100%;
                                left: rem(-40);
                                width: rem(288);
                                border: rem(3) solid #27a3dd;
                                padding: rem(34) rem(40);
                                background: #ffffff;
                                border-radius: rem(20);
                            }

                            &-item {
                                &:not(:last-child) {
                                    margin-bottom: rem(17);
                                }
                            }

                            &-link {
                                cursor: pointer;
                                font-family: "Neometric Alt", sans-serif;
                                font-size: rem(14);
                                font-weight: 600;
                                line-height: 1.4;
                                color: #000000;
                                transition: all 0.5s ease-in-out;

                                &:hover {
                                    color: #29a4dd;
                                }
                            }
                        }
                    }
                }

                .header__menu-link {
                    font-family: "Neometric Alt", sans-serif;
                    font-size: rem(14);
                    font-weight: 600;
                    line-height: 1.4;
                    color: var(--black-color);
                    transition: all 0.5s ease-in-out;

                    &:hover {
                        color: var(--lightblue-color);
                    }
                }
            }

            @media (max-width: 991px) {
                margin-bottom: rem(50);

                .header__menu-item {
                    position: relative;

                    &:not(:last-child) {
                        margin-right: 0;
                        margin-bottom: rem(28);
                    }

                    &._dropdown {

                        .header__menu-link {
                            position: relative;

                            &::before {
                                content: " ";
                                display: block;
                                position: absolute;
                                top: 50%;
                                left: rem(-12);
                                width: rem(5);
                                height: rem(11);
                                transform: translateY(-50%);
                                cursor: pointer;
                                margin-top: rem(-1);
                                background-image: url("../img/global/arrow.svg");
                                background-size: cover;
                                background-repeat: no-repeat;
                            }
                        }

                        &:hover>.header__menu-sub-list {
                            display: block;
                        }

                        &:hover>.header__menu-link {
                            &::before {
                                margin-top: rem(-6);
                                transform: rotate(90deg);
                            }
                        }
                    }
                }

                .header__menu-link {
                    font-family: "Neometric Alt", sans-serif;
                    font-size: rem(18);
                    font-weight: 600;
                    line-height: 1.4;
                    color: var(--black-color);
                    transition: all 0.5s ease-in-out;

                    &:hover {
                        color: var(--lightblue-color);
                    }
                }
            }
        }

        &-address {
            display: none;

            @media (max-width: 991px) {
                display: block;
                margin-bottom: rem(50);

                &__title {
                    display: block;
                    margin-bottom: rem(12);
                    font-size: rem(16);
                    line-height: 1.4;
                }

                &__item {
                    display: flex;
                    align-items: center;
                    margin-bottom: rem(12);
                    line-height: 1.4;

                    span {
                        display: inline-block;
                        margin-right: rem(8);
                        background-size: contain;
                        background-repeat: no-repeat;

                        &.ic-clock {
                            width: rem(16);
                            height: rem(16);
                            background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='8' cy='8' r='7.5' stroke='%232E8FBC'/%3E%3Cpath d='M8 4V9.6L11.2 10.8' stroke='%232E8FBC'/%3E%3C/svg%3E");
                        }

                        &.ic-mail {
                            width: rem(16);
                            height: rem(11);
                            background-image: url("data:image/svg+xml,%3Csvg width='18' height='13' viewBox='0 0 18 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1V12H17V1M1 1H17M1 1L8.74194 7.36842L17 1' stroke='%232E8FBC'/%3E%3C/svg%3E");
                        }
                    }

                    a {
                        color: var(--black-color);
                    }
                }
            }
        }

        &-phone {
            display: none;

            @media (max-width: 991px) {
                display: block;

                &__link {
                    margin-bottom: rem(8);
                    font-family: "Neometric Alt", sans-serif;
                    font-size: rem(16);
                    font-weight: 600;
                    line-height: 1.4;
                    color: var(--black-color);
                }

                &__text {
                    font-size: rem(14);
                }
            }
        }
    }

    &__menu-button {
        cursor: pointer;
        border: 0;
        transition: all 0.5s ease-in-out;
        background-color: transparent;

        @media (min-width: 992px) {
            display: none;
        }

        &:hover {
            opacity: 0.8;
        }
    }
}



